	module.exports = {"data":{"markdownRemark":{"html":"<p>They say to stay away<br>\nFrom the house by the mouth of the walnut lagoon<br>\nAll of the town gossip about<br>\nThe girl when laughs at the moon  </p>\n<p>Sister June is drunk in her room<br>\nIn The Governor Seat is a leech<br>\nWearing buffalo skin<br>\nThe farmer skipped town<br>\nBut his kids float around<br>\nAnd Alas,<br>\nThe girl who laughs at the moon  </p>\n<p>When the light hides<br>\nThe stars in the sky<br>\nIts easy to loose yourself in a dream you think is true<br>\nSo when its burning out<br>\nYou either lower your snout<br>\nOr you lookup, and you laugh at the moon  </p>\n<p>On a warm eve<br>\nI start to believe<br>\nThis Old World<br>\nAint gonna twirl to the beat of yer tune<br>\nSo, I chastise the town<br>\nAnd they call me a clown<br>\nLike the girl who laughs at the moon  </p>\n<hr>\n<p>Chords:</p>\n<pre><code>&#x3C;Cm>When the light&#x3C;/Cm> &#x3C;A#>hides&#x3C;/A#>  \n&#x3C;G#7>The stars in the&#x3C;/G#7> &#x3C;G>sky&#x3C;/G>  \n&#x3C;Cm>Its easy to&#x3C;/Cm> &#x3C;A#>loose yourself in a&#x3C;/A#> &#x3C;G#7>dream you think is&#x3C;/G#7> &#x3C;A#>true&#x3C;/A#>  \n&#x3C;Cm>So when its burning&#x3C;/Cm> &#x3C;A#>out&#x3C;/A#>  \n&#x3C;G#7>You either lower your&#x3C;/G#7> &#x3C;C#dim>snout&#x3C;/C#dim>  \n&#x3C;F7>Or you lookup&#x3C;F7>, &#x3C;A#7>and you laugh at&#x3C;/A#7> &#x3C;D#>the moon&#x3C;/D#>  \n</code></pre>","frontmatter":{"title":"The Girl Who Laughs At The Moon"},"fields":{"date":null}}},"pathContext":{"slug":"/songs/the-girl-who-laughs-at-the-moon/"}}

